@import 'components/variables';

.section-transferencias {
  padding: 5% 0;
  .title-page {
    font-size: 3.5rem;
    font-weight: 900;
    color: $violet;
  }
  .content-detalles {
    @media (min-width: 768px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      
    }
    .fila1 {
      display: flex;
      align-items: center;
      margin-right: 1rem;
      p {
        margin-bottom: 0;
        color: $text-gray;
        font-size: 1.2rem;
        font-weight: bold;
      }
    }
    .fila3 {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-right: 1rem;
      .img-icons {
        width: 2rem;
        margin-right: .8rem;
      }
      .form-select {
        width: 70px;
        border: none;
        padding: .375rem 1.4rem .375rem .75rem;
        @media (min-width: 425px) {
          width: 100px;
        }
        &:focus {
          border-color: $violet;
          outline: 0;
          box-shadow: 0 0 0 0.25rem rgba($color: $violet, $alpha: 0.5);
        }
      }
      .vistas {
        text-align: center;
        display: flex;
        .img-icons2 {
          width: 2rem;
          cursor: pointer;
        }
      }
    }
  }
  .accordion-button:not(.collapsed) {
    color: $violet;
    font-weight: bold;
    background-color: #f0d5f8;
  }
  .accordion-button {
    font-weight: bold;
    &:focus {
      border-color: #86b7fe;
      box-shadow: none
    }
  }
  .accordion-item:last-of-type {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
}
  .clean-filtro {
    padding: 2rem;
    text-align: center;
    font-weight: bold;
    font-size: 1.3rem;
    border: 1px solid rgba(0,0,0,.125);
    border-top: 0;
    border-bottom-right-radius: .25rem;
    border-bottom-left-radius: .25rem;
    a {
      color: $text-gray;
    }
  }
  .search-more {
    position: relative;
    padding: 1rem;
    background-color: $violet;
    border-radius: 5px;
    .text-big {
      color: $white;
      font-size: 2rem;
      font-weight: 700;
      margin-bottom: 0;
      line-height: 1;
    }
    a {
      color: $white;
      font-size: .8rem;
      transition: all .3s;
      &:hover {
        color: $yellow;
      }
    }
    img {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 10%;
      @media (min-width: 992px) {
        width: 30%;
      }
    }
  }
  .img-destacada {
    position: relative;
    border: 1px solid $gray-light;
    .content {
      overflow: hidden;
      display: flex;
      align-items: center;
      @media (min-width: 768px) {
        position: absolute;
        width: 100%;
        height: 100%;
      }
      @media (max-width: 767.98px) {
        height: 40vw;
      }
      .destacado {
        background-color: $orange;
        text-align: center;
        color: $white;
        font-weight: bold;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        font-size: .7rem;
        padding: .4rem;
        position: absolute;
        top: 15px;
        left: 0;
        @media (min-width: 768px) {
          font-size: .9rem;
        }
      }
      .img-ppal {
        transition: all 0.3s;
        &:hover {
          transform: scale(1.2);
        }
      }
    }
  }
  .detalles-transferencia {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 1px solid $gray-light;
    .valoracion {
      margin-bottom: 1rem;
      img {
        width: 10px;
        @media (min-width: 375px) {
          width: 15px;
        }
        @media (min-width: 482px) {
          width: 23px;
        }
      }
    }
    .name-transferencia {
      font-size: 1.5rem;
      font-weight: bold;
      color: $text-gray;
      margin-bottom: 0;
      transition: all 0.3s;
      &:hover {
        color: $violet;
      }
      p {
        margin-top: 1rem;
      }
    }
    .icons-users {
      margin-bottom: 1rem;
      .fa-user,
      .fa-users {
        color: $violet;
        font-size: 1.2rem;
      }
      .fa-users {
        margin-left: 1rem;
      }
    }
    ul {
      list-style: none;
      padding-left: 0;
      font-size: .8rem;
      a {
        color: $text-gray;
        &:hover {
          color: $violet;
        }
      }
    }
  }
  .price-transferencia {
    border: 1px solid $gray-light;
    .price {
      font-size: 1.2rem;
      margin-bottom: 0;
    }
    .denominacion {
      font-weight: bold;
      strong {
        color: $violet;
        font-size: 1.7rem;
        font-weight: 700;
      }
    }
    .descrip-reserva {
      margin-bottom: 0;
    }
    .btn-violet-white {
      font-size: .8rem;
      margin-top: 2rem;
    }
  }
}

#modalDetalleTransferencia {
  z-index: 3050;
  .modal-content {
    .modal-header {
      .modal-title {
        font-weight: bold;
        color: $violet;
        line-height: 0.7;
        strong {
          font-size: 1.2rem;
          color: $text-gray;
          font-weight: 600;
        }
      }
    }
    .modal-body {
      .col-name-transf {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .name-transf {
          font-size: 1.5rem;
          font-weight: bold;
          color: $text-gray;
          margin-bottom: 0;
          @media (min-width: 992px) {
            font-size: 2rem;
          }
          strong {
            color: $violet;
          }
        }
      }
      .perfil-proveedor {
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          width: 10%;
          border-radius: 50%;
          border: 2px solid $violet;
          margin-right: .8rem;
          @media (min-width: 768px) {
            width: 15%;
          }
        }
      }
      .caracteristicas-transf {
        font-size: .8rem;
        ul {
          list-style: circle;
          padding-left: 2rem;
        }
      }
      .form-control:focus {
        color: #212529;
        background-color: #fff;
        border-color: rgba($color: $violet, $alpha: 0.5);
        outline: 0;
        box-shadow: 0 0 0 0.25rem rgba($color: $violet, $alpha: 0.5);
      }
    }
  }
}